import React, {useEffect, useRef, useState} from 'react';

import './style.css';
import './zoomIn.css';

import classnames from 'classnames';
import {Animated} from 'react-animated-css';


const isInViewport = function (elem) {

  if(!elem) return;

  const bounding = elem.getBoundingClientRect();
  return (
    bounding.top >= 0 &&
    bounding.left >= 0 &&
    bounding.bottom <= (window.innerHeight || document.documentElement.clientHeight) &&
    bounding.right <= (window.innerWidth || document.documentElement.clientWidth)
  );
};

const InfoCard = ({
    bgColor,
    textData,
    listStyle,
    className
}) => {
  const animateRef = useRef(null);
  const [show, setShow] = useState(false);

  const handleScroll = () => {
    if (isInViewport(animateRef.current)) {
      setShow(true);//animateRef.innerHTML = '<img src="' + animateRef.getAttribute('data-image') + '">';
    }
  };

  useEffect(() => {
    window.addEventListener('scroll', handleScroll, false);

    return () => window.removeEventListener('scroll', handleScroll, false);
  }, []);

    return (
      <span
        style={{ flex: 1, width: '47.5%', marginLeft: 20 }}
        ref={ref => animateRef.current = ref}
      >
        {show && <Animated
          animateOnMount
          animationIn="zoomIn"
          animationOut="zoomOut"
          isVisible={true}
          className="inforCardContent">
          <div
            className={classnames('InfoCard', className)} />
            <ul
              style={{
                backgroundColor : bgColor,
                listStyleType : listStyle || 'none',
                paddingLeft : (listStyle) ? '15px' : '0'
              }}
            >
              <div className="info_content">{textData}</div>
            </ul>
      </Animated>}
      </span>
    )
};

export default InfoCard;
