import React from 'react';
import Spinner from 'react-spinner-material';

const BookLast = ({ loading, texts = [] }) => {
  if (loading) {
    return (
      <div style={{
        flex: 1,
        display: 'flex',
        height: '100%',
        justifyContent: 'center',
        flexDirection: 'column',
        paddingTop: '30px',
        width: '100%',
        textAlign: 'center',
        alignItems: 'center',
      }}>
        <Spinner size={120} spinnerColor="#a6815b" spinnerWidth={2} visible={true} />
      </div>
    );
  }

  const findText = (key) => {
    return texts.find(txt => txt.key === key).text;
  };

    return(
        <div style={{paddingTop: '30px'}}>
            <p>
              {findText('BOOKING_LAST_STEP_DESCRIPTION')}
            </p>
        </div>
    )
};

export default BookLast;
